@import '../_includes';
// <!-- TODO: svg -->
// outlines

.section-inner {
  max-width: 1200px;
  margin: 0 auto;

  @include font-lotto;
}

.section-outer {
  padding: 0 20px;
}

.section-header {
  position: relative;
  color: white;
  background: url('../../images/bc.jpeg') center no-repeat;
  background-size: cover;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background: #142729;
    opacity: 0.695312;
  }

  .section-inner {
    position: relative;
    z-index: 15;
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  &-logo {
    flex-shrink: 1;
    align-self: start;
    margin-top: 30px;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
    cursor: pointer;
  }

  &-wrapper {
    @media (min-width: $screen_size_md_min) {
      &-title {
        font-size: 55px;
      }

      &-subtitle {
        width: 50%;
        font-size: 18px;
      }

      &-form {
        &-input {
          padding-right: 40px;
        }
      }
    }

    @media (max-width: $screen_size_sm_max) {
      &-title {
        font-size: 45px;
      }

      &-subtitle {
        width: 80%;
      }

      &-form {
        flex-direction: column;

        &-input {
          margin-bottom: 10px;
        }
      }
    }

    // until
    @media (max-width: $screen_size_xs_max) {
      &-title {
        margin-bottom: 10px;
        font-size: 35px;
      }

      &-subtitle {
        margin-bottom: 20px;
      }
    }
    // after
    @media (min-width: $screen_size_sm_min) {
      &-title {
        margin-bottom: 22px;
      }

      &-subtitle {
        margin-bottom: 40px;
      }
    }

    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    text-align: center;

    &-title {
      font-weight: 400;
      line-height: 60px;
    }

    &-subtitle {
      font-weight: 400;
      line-height: 32px;
      text-align: center;
    }

    &-form {
      display: flex;
      align-self: stretch;
      margin: 0 40px;

      &-input {
        flex-grow: 1;

        input {
          width: 100%;
          padding: 15px;
          padding-left: 25px;
          color: white;
          background-color: rgba(255, 255, 255, 0.205);
          border: none;
          border-radius: 4px;
          outline: none;
          transition: 0.2s;

          @include font-lotto;

          &:focus,
          &:hover {
            background-color: rgba(255, 255, 255, 0.287);
          }
        }

        input::placeholder {
          @include font-lotto;

          color: white;
        }
      }
    }
  }
}

.btn {
  @include font-lotto;

  padding: 15px;
  color: #464646;
  cursor: pointer;
  background: white;
  border: none;
  border-radius: 4px;
  outline: none;
  transition: 0.2s;

  &:hover {
    background: rgb(240, 240, 240);
  }
}

.btn.focuse {
  color: white;
  background: linear-gradient(90deg, #fe985b, #fe4fa7);

  &:hover {
    opacity: 0.9;
  }
}

.btn.border {
  border: solid 1px #979797;
}

.section-promo {
  margin-bottom: 110px;

  @media (max-width: $screen_size_sm_max) {
    .section-inner {
      flex-direction: column;
    }
  }

  .section-inner {
    display: flex;
    padding-top: 110px;
  }

  &-1 {
    flex-basis: 50%;

    @media (min-width: $screen_size_md_min) {
      padding-right: 10px;
    }

    &-title {
      // until
      @media (max-width: $screen_size_sm_max) {
        margin-bottom: 25px;
      }

      // after
      @media (min-width: $screen_size_md_min) {
        margin-bottom: 55px;
      }

      font-size: 36px;
      font-weight: 400;
      line-height: 51px;
    }

    &-features {
      @media (min-width: $screen_size_md_min) {
        width: 80%;
      }

      &-item {
        &-title {
          margin-bottom: 12px;
          font-size: 18px;
          font-weight: 400;
          line-height: 22px;
        }

        &-text {
          margin-bottom: 10px;
          font-size: 16px;
          font-weight: 300;
          line-height: 28px;
        }
      }
    }
  }

  &-video {
    flex-basis: 50%;

    @media (max-width: $screen_size_sm_max) {
      order: -1;
      margin-bottom: 30px;
    }

    @media (min-width: $screen_size_md_min) {
      padding-top: 30px;
    }

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
}

.section-features {
  padding-top: 90px;
  margin-bottom: 130px;
  border-top: solid 1px #e8e8e8;

  &-wrapper {
    display: flex;
    // until
    @media (max-width: $screen_size_sm_max) {
      flex-direction: column;
      align-items: center;

      &-item {
        width: 80%;

        & + & {
          margin-top: 30px;
        }
      }
    }

    // after
    @media (min-width: $screen_size_md_min) {
      &-item:not(:last-child) {
        margin-right: 20px;
      }
    }

    &-item {
      flex-basis: 33%;
      text-align: center;

      &-title {
        margin: 20px 0;
        font-size: 18px;
        font-weight: 400;
        line-height: 22px;
        color: #414141;
      }

      &-text {
        font-size: 16px;
        font-weight: 400;
        line-height: 28px;
        color: #6c6c6c;
      }
    }
  }
}

.section-founder {
  padding-top: 140px;
  background: #f6fafd;
  // until
  @media (max-width: $screen_size_sm_max) {
    padding-bottom: 140px;
  }

  &-wrapper {
    display: flex;
    // until
    @media (max-width: $screen_size_sm_max) {
      flex-direction: column;

      &-photo {
        margin-bottom: 20px;
        text-align: center;

        img {
          display: block;
          width: 100%;
        }
      }
    }

    // after
    @media (min-width: $screen_size_md_min) {
      &-about {
        padding-left: 50px;
      }
    }

    &-about {
      display: flex;
      flex-direction: column;
      justify-content: center;

      &-text {
        margin-bottom: 30px;
        font-size: 24px;
        font-weight: 400;
        line-height: 40px;
        color: #414141;
      }

      &-name,
      &-prof {
        margin-bottom: 10px;
        font-size: 24px;
        font-weight: 600;
        line-height: 29px;
        color: #414141;
      }
    }
  }
}

.section-plans {
  padding-top: 115px;
  margin-bottom: 150px;

  &-wrapper {
    display: flex;
    flex-direction: column;
    max-width: 750px;
    margin: 0 auto;
    text-align: center;

    &-title {
      margin-bottom: 30px;
      font-size: 36px;
      font-weight: 400;
      line-height: 51px;
      color: #414141;
    }

    &-text {
      margin-bottom: 70px;
      font-size: 16px;
      font-weight: 400;
      line-height: 28px;
      color: #6c6c6c;
    }

    &-plans {
      display: flex;
      // until
      @media (max-width: $screen_size_sm_max) {
        flex-direction: column;
        align-items: center;

        &-item {
          width: 300px;
        }
      }
      // after
      @media (min-width: $screen_size_md_min) {
      }

      &-item {
        flex-basis: 50%;
        padding-bottom: 50px;

        &.focused {
          background: #f6fafd;
        }

        &-name {
          margin-top: 50px;
          margin-bottom: 30px;
          font-size: 14px;
          font-weight: 400;
          line-height: 17px;
          color: #414141;
          text-transform: uppercase;
        }

        &-price {
          margin-bottom: 40px;
          font-size: 14px;
          font-weight: 400;
          line-height: 17px;
          color: #414141;

          span {
            font-size: 72px;
            font-weight: 400;
            line-height: 87px;
            color: #fe4fa7;
          }
        }

        &-text {
          width: 70%;
          margin: 0 auto;
          margin-bottom: 15px;
          font-size: 16px;
          font-weight: 400;
          line-height: 28px;
          color: #6c6c6c;
        }
      }
    }
  }
}

.section-banner {
  padding-top: 120px;
  padding-bottom: 120px;
  color: white;
  text-align: center;
  background: linear-gradient(90deg, #fe985b, #fe4fa7);

  &-content {
    &-title {
      margin-bottom: 35px;
      font-size: 55px;
      font-weight: 400;
      line-height: 60px;
    }

    &-text {
      // after
      @media (min-width: $screen_size_md_min) {
        width: 60%;
      }

      margin: 0 auto;
      margin-bottom: 40px;
      font-size: 20px;
      font-weight: 400;
      line-height: 30px;
    }
  }
}

.section-footer {
  margin: 85px 0;
  text-align: center;

  &-wrapper {
    display: flex;
    flex-direction: column;
  }

  &-title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
  }

  &-links {
    // display: flex;
    // justify-content: space-around;
    // until
    @media (max-width: $screen_size_md_max) {
      display: flex;
      flex-direction: column;

      &-item + &-item {
        margin-top: 10px;
      }
    }

    // after
    @media (min-width: $screen_size_lg_min) {
      &-item + &-item {
        margin-left: 100px;
      }
    }

    &-item {
      &:hover {
        color: #fa5ad2;
      }

      font-size: 14px;
      font-weight: 400;
      font-weight: 600;
      line-height: 20px;
      color: #464646;
      text-align: center;
      text-decoration: none;
      text-transform: uppercase;
      transition: 0.2s;
    }
  }

  &-social {
    margin: 30px 0;

    &-item {
      margin: 0 15px;
      font-size: 25px;
      color: #363636;
      text-decoration: none;
      transition: 0.2s;

      &:hover {
        color: #fa5ad2;
      }
    }
  }

  &-copyright {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #363636;

    // until
    @media (max-width: $screen_size_md_max) {
      flex-direction: column;

      &-item + &-item {
        margin-top: 20px;
      }
    }

    // after
    @media (min-width: $screen_size_lg_min) {
      &-item + &-item {
        margin-left: 40px;
      }
    }

    &-item + &-item {
      color: #363636;
      text-decoration: none;
      transition: 0.2s;
    }
  }
}

a.section-footer-copyright-item:hover {
  color: #fa5ad2;
}
